<template>
  <div class="components-container">
    <!-- <div class="info"></div> -->
    
    <h3 class="title"></h3>
    <div class="main">
         <div class="box">
                    <p></p>
                    <div class="content">
                        <el-upload
                        class="upload-demo"
                        action="/api/upload/"
                        :on-success="handleSuccess"
                        :on-remove="handleRemove"
                        :file-list="fileList"
                        list-type="picture"
                        :limit='1'>
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                    </el-upload>
                    </div>
                
            </div>

          <div class="box">
            <p></p>
            <el-input type="text" placeholder="" v-model="title"></el-input>
          </div>

          <div class="box">
              <p></p>
              <el-date-picker
                v-model="date"
                type="datetime"
                placeholder="">
              </el-date-picker>
          </div>
  
          <div class="editor-container">
            <p></p>
            <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue"></UE>
            <!-- <UE :defaultMsg=defaultMsg :config=config :id=ue2 ref="ue2"></UE> -->
          </div>
           <el-button type="primary" class="pull-right" @click="submit()"></el-button>
    </div>
    
    
  </div>
</template>
<style scoped lang='less'>
.info {
  border-radius: 10px;
  line-height: 20px;
  padding: 10px;
  margin: 10px;
  background-color: #ffffff;
}
.editor-container {
  margin-bottom: 20px;
}
.main{
  padding-left: 40px;
  padding-bottom: 40px;
}
.box {
  width: 500px;
  margin: 10px 0 20px;
}

</style>
<script>
import UE from "components/ue/ue.vue";
import { news_select_id,news_edit } from 'api/news';
import { delete_pic } from 'api/works';

export default {
  components: { UE },
  async mounted(){
       //id
     this.news_id = this.$route.query.id*1;
     let res = await news_select_id({id: this.news_id});
        
    //
     if(res.pic_url)  this.fileList.splice(0,1,{url:res.pic_url});  //
     this.title = res.news_title;
     this.content = res.news_content
     this.date = res.news_date;
     this.plainText = res.news_plainText;
     this.$refs.ue.insertHtml(this.content);
     this.url = res.pic_url;
     this.pic_name = res.pic_name;
  },
  data() {
    return {
      fileList: [], //
      defaultMsg: "",
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350,
      },
      ue1: "ue1", // id
      title: "", //
      content: "", //
      date: "", //
      plainText:"", //
      news_id:-1, //id
      url:'',
      pic_name:''
    };
  },
  methods: {
    submit() {
      this.content = this.$refs.ue.getUEContent(); // 
      this.plainText =  this.$refs.ue.getContentTxt(); //
      
      news_edit({
        title:this.title,
        date:this.date,
        content:this.content,
        plainText:this.plainText,
        id:this.news_id,
        url:this.url,
        pic_name:this.pic_name
        });
    },
    handleSuccess(data) {
        this.url = data;
        this.pic_name = data.replace('/api/static/img/','');
    },
    handleRemove() {
        //
        delete_pic({pic_name:this.pic_name});
        this.url = "";
        this.pic_name = "";
    },
  }
};
</script>






